import { NavLink } from "react-router-dom";
import { useEffect, useRef, useState } from "react";

function Join(){
  const initVal={
    userid : '',
    pwd1 : '',
    pwd2 : '',
    email: '',
    comments: '',
    gender: ''
  }
  const [val, setVal] = useState(initVal);
  const [err, setErr] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);
  const [success, setSuccess] = useState(false);

  const handleChange = e => {   
    const {name, value} = e.target;     
    setVal({...val, [name]: value});     
  }

  const handleCheck = e => {
    //    name  
    const {name} = e.target;
    //    boolean 
    const isCheck = e.target.checked;
    //val state   , boolean value 
    setVal({...val, [name]: isCheck});  
  }

  //submit   
  const handleSubmit = e =>{ 
    e.preventDefault();   
    setIsSubmit(true);
    setErr(check(val));  
    console.log(val);
  }

  //   
  const check = val=>{
    let errs = {};  
    let eng = /[a-zA-Z]/;
    let num = /[0-9]/;
    let spc = /[!@#$%^&*]/;

    if( !val.userid || val.userid.length <5 ){
      errs.userid=' 5 ';
    } 
    if( !val.pwd1 || val.pwd1.length<5 || !eng.test(val.pwd1) || !num.test(val.pwd1) || !spc.test(val.pwd1)){
      errs.pwd1=' 5 , ,,  ';
    }
    if( !val.email || val.email.length <8 || !/@/.test(val.email) ){
      errs.email=' 8 ';
    }
    if( !val.pwd2 || val.pwd1 !== val.pwd2 ){
      errs.pwd2='   ';
    }
    if( !val.comments || val.comments.length <10 ){
      errs.comments=' 10 ';
    }
    if( !val.gender ){
      errs.gender=' ';
    }
    return errs;
  }
 
  useEffect(()=>{        
    console.log(err);
    const len =  Object.keys(err).length;
    if(len === 0 && isSubmit){
      console.log(' ');
      setSuccess(true);     
    }else{
      console.log(' ');
      setSuccess(false);
    }
  },[err]);

  return (
    <main className='content join'>
        <div className="subTitle">
          <div className="inner">
            <h1><NavLink to="/gallery">Join</NavLink></h1>
          </div>
        </div>
      <div className="inner">
        {success ? <div> .</div> : null }
        <form onSubmit={handleSubmit}>
          <fieldset>
            <legend className='h'>   </legend>

            <table>
              <caption className='h'> </caption>
              <tbody>
                {/* userid */}
                <tr>
                  <th scope='row'>
                    <label htmlFor="userid">USER ID</label>
                  </th>
                  <td>
                    <input 
                      type="text" 
                      id='userid'
                      name='userid'
                      placeholder=' '                    
                      onChange={handleChange}
                    />
                    <span className='err'>{err.userid}</span>
                  </td>
                </tr> 

                {/* password */}
                <tr>
                  <th scope='row'>
                    <label htmlFor="pwd1">PASSWORD</label>
                  </th>
                  <td>
                    <input 
                      type="password" 
                      id='pwd1'
                      name='pwd1'
                      placeholder=' '                  
                      onChange={handleChange}
                    />
                    <span className='err'>{err.pwd1}</span>
                  </td>
                </tr>

                {/* re password */}
                <tr>
                  <th scope='row'>
                    <label htmlFor="pwd2">RE-PASSWORD</label>
                  </th>
                  <td>
                    <input 
                      type="password" 
                      id='pwd2'
                      name='pwd2'
                      placeholder=' '                  
                      onChange={handleChange}
                    />
                    <span className='err'>{err.pwd2}</span>
                  </td>
                </tr>

                {/* email */}
                <tr>
                  <th scope='row'>
                    <label htmlFor="email">E-MAIL</label>
                  </th>
                  <td>
                    <input 
                      type="text" 
                      id='email'
                      name='email'
                      placeholder='  '         
                      onChange={handleChange}
                    />
                    <span className='err'>{err.email}</span>
                  </td>
                </tr>

                {/* comments */}
                <tr>
                  <th scope='row'>
                    <label htmlFor="comments">COMMENTS</label>
                  </th>
                  <td>
                    <textarea
                      col='30'
                      row='10'
                      id='comments'
                      name='comments'
                      placeholder='  2'                  
                      onChange={handleChange}
                    ></textarea>
                    <span className='err'>{err.comments}</span>
                  </td>
                </tr>

                {/* gender */}
                <tr>
                  <th scope='row'>
                    GENDER
                  </th>
                  <td>
                    <label htmlFor='male'>Male</label>
                    <input 
                      type="radio" 
                      id='male' 
                      name='gender' 
                      onChange = {handleCheck}
                    />

                    <label htmlFor='femal'>Female</label>
                    <input 
                      type="radio" 
                      id='female' 
                      name='gender' 
                      onChange = {handleCheck} 
                    />
                    <span className='err'>{err.gender}</span>
                  </td>
                </tr>

                {/* btn set */}
                <tr>
                  <th className="btnSet" colSpan='2'>
                    <input type="reset" value='CANCEL' />
                    <input type="submit" value='SEND' />
                  </th>
                </tr> 
              </tbody>       
            </table>
          </fieldset>
        </form>
      </div>
    </main>
  )
}

export default Join;